"use client";

import React, { useState } from "react";
import { Check, Copy, Terminal, FileCode, FileJson, FileText, Folder } from "lucide-react";

interface CodeBlockProps {
  code: string;
  language?: "bash" | "javascript" | "typescript" | "json" | "python" | "ruby" | "text" | "output";
  filename?: string;
  showLineNumbers?: boolean;
  title?: string;
  copyable?: boolean;
}

const languageConfig: Record<string, { label: string; color: string; icon: typeof Terminal }> = {
  bash: { label: "Terminal", color: "from-green-500/20 to-green-600/10 border-green-500/30", icon: Terminal },
  javascript: { label: "JavaScript", color: "from-yellow-500/20 to-yellow-600/10 border-yellow-500/30", icon: FileCode },
  typescript: { label: "TypeScript", color: "from-blue-500/20 to-blue-600/10 border-blue-500/30", icon: FileCode },
  json: { label: "JSON", color: "from-orange-500/20 to-orange-600/10 border-orange-500/30", icon: FileJson },
  python: { label: "Python", color: "from-sky-500/20 to-sky-600/10 border-sky-500/30", icon: FileCode },
  ruby: { label: "Ruby", color: "from-red-500/20 to-red-600/10 border-red-500/30", icon: FileCode },
  text: { label: "Plain Text", color: "from-gray-500/20 to-gray-600/10 border-gray-500/30", icon: FileText },
  output: { label: "Output", color: "from-purple-500/20 to-purple-600/10 border-purple-500/30", icon: Terminal },
};

// Simple syntax highlighting
function highlightSyntax(code: string, language: string): React.ReactNode[] {
  const lines = code.split("\n");

  return lines.map((line, i) => {
    if (language === "output" || language === "text") {
      return <span key={i} className="text-white/60">{line}</span>;
    }

    if (language === "bash") {
      // Highlight comments
      if (line.trim().startsWith("#")) {
        return <span key={i} className="text-white/40 italic">{line}</span>;
      }
      // Highlight the command prefix ($)
      if (line.trim().startsWith("$")) {
        const [prefix, ...rest] = line.split(" ");
        return (
          <span key={i}>
            <span className="text-purple-400">{prefix}</span>
            <span className="text-green-400"> {rest.join(" ")}</span>
          </span>
        );
      }
      // Regular commands
      const parts = line.split(" ");
      const cmd = parts[0];
      const args = parts.slice(1).join(" ");

      // Check for flags
      const highlightedArgs = args.split(/(\s+)/).map((part, j) => {
        if (part.startsWith("--") || part.startsWith("-")) {
          return <span key={j} className="text-cyan-400">{part}</span>;
        }
        if (part.startsWith("http://") || part.startsWith("https://") || part.includes(".onion")) {
          return <span key={j} className="text-blue-400 underline">{part}</span>;
        }
        return <span key={j} className="text-green-400">{part}</span>;
      });

      return (
        <span key={i}>
          <span className="text-yellow-400">{cmd}</span>
          {args && <span> </span>}
          {highlightedArgs}
        </span>
      );
    }

    if (language === "javascript" || language === "typescript") {
      // Simple JS/TS highlighting
      let highlighted = line;

      // Comments
      if (line.trim().startsWith("//")) {
        return <span key={i} className="text-white/40 italic">{line}</span>;
      }

      // Process the line for syntax highlighting
      const elements: React.ReactNode[] = [];
      let remaining = line;
      let keyIndex = 0;

      // Keywords
      const keywords = ["const", "let", "var", "function", "return", "if", "else", "import", "export", "from", "require", "async", "await", "class", "extends", "new", "this", "try", "catch", "throw", "typeof", "instanceof"];
      const keywordRegex = new RegExp(`\\b(${keywords.join("|")})\\b`, "g");

      // String regex
      const stringRegex = /(['"`])((?:\\.|(?!\1)[^\\])*)\1/g;

      // Number regex
      const numberRegex = /\b(\d+)\b/g;

      // Split and process
      const tokens: { type: string; value: string }[] = [];
      let lastIndex = 0;

      // Find strings first
      let match;
      const allMatches: { type: string; start: number; end: number; value: string }[] = [];

      stringRegex.lastIndex = 0;
      while ((match = stringRegex.exec(remaining)) !== null) {
        allMatches.push({ type: "string", start: match.index, end: match.index + match[0].length, value: match[0] });
      }

      keywordRegex.lastIndex = 0;
      while ((match = keywordRegex.exec(remaining)) !== null) {
        // Check if inside a string
        const inString = allMatches.some(m => match!.index >= m.start && match!.index < m.end);
        if (!inString) {
          allMatches.push({ type: "keyword", start: match.index, end: match.index + match[0].length, value: match[0] });
        }
      }

      numberRegex.lastIndex = 0;
      while ((match = numberRegex.exec(remaining)) !== null) {
        const inString = allMatches.some(m => match!.index >= m.start && match!.index < m.end);
        const inKeyword = allMatches.some(m => m.type === "keyword" && match!.index >= m.start && match!.index < m.end);
        if (!inString && !inKeyword) {
          allMatches.push({ type: "number", start: match.index, end: match.index + match[0].length, value: match[0] });
        }
      }

      // Sort by position
      allMatches.sort((a, b) => a.start - b.start);

      // Build elements
      let pos = 0;
      allMatches.forEach((m, idx) => {
        if (m.start > pos) {
          elements.push(<span key={`text-${keyIndex++}`} className="text-white/80">{remaining.slice(pos, m.start)}</span>);
        }
        if (m.type === "string") {
          elements.push(<span key={`str-${keyIndex++}`} className="text-green-400">{m.value}</span>);
        } else if (m.type === "keyword") {
          elements.push(<span key={`kw-${keyIndex++}`} className="text-purple-400">{m.value}</span>);
        } else if (m.type === "number") {
          elements.push(<span key={`num-${keyIndex++}`} className="text-orange-400">{m.value}</span>);
        }
        pos = m.end;
      });

      if (pos < remaining.length) {
        elements.push(<span key={`end-${keyIndex++}`} className="text-white/80">{remaining.slice(pos)}</span>);
      }

      return <span key={i}>{elements.length > 0 ? elements : <span className="text-white/80">{line}</span>}</span>;
    }

    if (language === "json") {
      // Simple JSON highlighting
      const elements: React.ReactNode[] = [];
      let keyIndex = 0;

      // Keys
      const keyRegex = /"([^"]+)":/g;
      // Strings
      const stringRegex = /:\s*"([^"]*)"/g;
      // Numbers
      const numberRegex = /:\s*(\d+)/g;
      // Booleans
      const boolRegex = /:\s*(true|false|null)/g;

      let remaining = line;
      let lastEnd = 0;

      // Find all keys
      let match;
      const matches: { type: string; start: number; end: number; value: string }[] = [];

      keyRegex.lastIndex = 0;
      while ((match = keyRegex.exec(remaining)) !== null) {
        matches.push({ type: "key", start: match.index, end: match.index + match[0].length - 1, value: `"${match[1]}"` });
      }

      stringRegex.lastIndex = 0;
      while ((match = stringRegex.exec(remaining)) !== null) {
        const valueStart = remaining.indexOf(`"${match[1]}"`, match.index + 1);
        if (valueStart !== -1) {
          matches.push({ type: "string", start: valueStart, end: valueStart + match[1].length + 2, value: `"${match[1]}"` });
        }
      }

      // Sort and render
      matches.sort((a, b) => a.start - b.start);

      let pos = 0;
      matches.forEach((m) => {
        if (m.start > pos) {
          elements.push(<span key={`t-${keyIndex++}`} className="text-white/60">{remaining.slice(pos, m.start)}</span>);
        }
        if (m.type === "key") {
          elements.push(<span key={`k-${keyIndex++}`} className="text-cyan-400">{m.value}</span>);
        } else if (m.type === "string") {
          elements.push(<span key={`s-${keyIndex++}`} className="text-green-400">{m.value}</span>);
        }
        pos = m.end;
      });

      if (pos < remaining.length) {
        elements.push(<span key={`e-${keyIndex++}`} className="text-white/60">{remaining.slice(pos)}</span>);
      }

      return <span key={i}>{elements.length > 0 ? elements : <span className="text-white/60">{line}</span>}</span>;
    }

    if (language === "python") {
      // Python highlighting
      if (line.trim().startsWith("#")) {
        return <span key={i} className="text-white/40 italic">{line}</span>;
      }

      const keywords = ["def", "class", "import", "from", "return", "if", "elif", "else", "for", "while", "try", "except", "with", "as", "lambda", "yield", "async", "await", "True", "False", "None"];
      let highlighted = line;

      keywords.forEach(kw => {
        const regex = new RegExp(`\\b${kw}\\b`, "g");
        highlighted = highlighted.replace(regex, `__KW_${kw}__`);
      });

      const elements: React.ReactNode[] = [];
      const parts = highlighted.split(/(__KW_\w+__)/);

      parts.forEach((part, idx) => {
        if (part.startsWith("__KW_") && part.endsWith("__")) {
          const kw = part.slice(5, -2);
          elements.push(<span key={idx} className="text-purple-400">{kw}</span>);
        } else {
          elements.push(<span key={idx} className="text-white/80">{part}</span>);
        }
      });

      return <span key={i}>{elements}</span>;
    }

    // Default
    return <span key={i} className="text-green-400">{line}</span>;
  });
}

export function CodeBlock({
  code,
  language = "bash",
  filename,
  showLineNumbers = false,
  title,
  copyable = true
}: CodeBlockProps) {
  const [copied, setCopied] = useState(false);
  const config = languageConfig[language] || languageConfig.text;
  const Icon = config.icon;
  const lines = code.split("\n");
  const shouldShowLineNumbers = showLineNumbers || lines.length > 5;

  // Clean code for copying (remove leading $ from bash commands)
  const cleanCodeForCopy = () => {
    if (language === "bash") {
      return code.split("\n").map(line => {
        if (line.trim().startsWith("$ ")) {
          return line.replace(/^\s*\$\s*/, "");
        }
        if (line.trim().startsWith("# ")) {
          return ""; // Remove comment lines from copy
        }
        return line;
      }).filter(Boolean).join("\n");
    }
    return code;
  };

  const handleCopy = async () => {
    try {
      await navigator.clipboard.writeText(cleanCodeForCopy());
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    } catch (err) {
      console.error("Failed to copy:", err);
    }
  };

  const highlightedLines = highlightSyntax(code, language);

  return (
    <div className="group relative rounded-lg overflow-hidden border border-white/10 bg-[#0a0a0a]">
      {/* Header */}
      <div className={`flex items-center justify-between px-4 py-2 bg-gradient-to-r ${config.color} border-b border-white/10`}>
        <div className="flex items-center gap-2">
          <Icon className="w-4 h-4 text-white/60" />
          <span className="text-xs font-medium text-white/70">
            {filename || title || config.label}
          </span>
          {filename && language !== "bash" && (
            <span className="text-[10px] px-1.5 py-0.5 rounded bg-white/10 text-white/50 uppercase tracking-wide">
              {language}
            </span>
          )}
        </div>

        {copyable && (
          <button
            onClick={handleCopy}
            className="flex items-center gap-1.5 px-2 py-1 rounded text-xs text-white/50 hover:text-white/80 hover:bg-white/10 transition-all"
            aria-label={copied ? "Copied" : "Copy to clipboard"}
          >
            {copied ? (
              <>
                <Check className="w-3.5 h-3.5 text-green-400" />
                <span className="text-green-400">Copied!</span>
              </>
            ) : (
              <>
                <Copy className="w-3.5 h-3.5" />
                <span>Copy</span>
              </>
            )}
          </button>
        )}
      </div>

      {/* Code Content */}
      <div className="relative overflow-x-auto">
        <pre className="p-4 text-sm font-mono leading-relaxed">
          {shouldShowLineNumbers ? (
            <div className="flex">
              {/* Line numbers */}
              <div className="select-none pr-4 text-right border-r border-white/10 mr-4">
                {lines.map((_, i) => (
                  <div key={i} className="text-white/20 text-xs leading-relaxed">
                    {i + 1}
                  </div>
                ))}
              </div>
              {/* Code */}
              <div className="flex-1">
                {highlightedLines.map((line, i) => (
                  <div key={i} className="leading-relaxed">
                    {line}
                  </div>
                ))}
              </div>
            </div>
          ) : (
            <div>
              {highlightedLines.map((line, i) => (
                <div key={i} className="leading-relaxed">
                  {line}
                </div>
              ))}
            </div>
          )}
        </pre>
      </div>
    </div>
  );
}

// Inline code component
export function InlineCode({ children }: { children: React.ReactNode }) {
  return (
    <code className="px-1.5 py-0.5 rounded bg-white/10 text-white/90 text-sm font-mono border border-white/5">
      {children}
    </code>
  );
}
